.experience {
  /* Grid display columns of 3 with the center column being just 1 thin strip */
  display: grid;
  grid-template-columns: 1fr 0fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "left center right";
  /* Center the grid */
  justify-items: center;
  align-items: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding-left: 2rem;
  padding-right: 2rem;
  border-bottom: 0.15rem solid #3c7a89;
  padding-bottom: 2rem;
}

.mobile-experience {
  display: flex;
  flex-direction: column;
  margin-left: 3rem;
  margin-right: 3rem;
  border-bottom: 0.15rem solid #3c7a89;
}

.mobile-experience h3,
.mobile-experience h4,
.mobile-experience h5 {
  margin: 0;
  margin-bottom: 0.5rem;
}

.experience-border {
  width: 0.5rem;
  height: 100%;
  background-color: #3c7a89;
  border-radius: 2rem;
}

.dark-mode .experience-header {
  color: #f5f5f5;
}

.experience-header {
  text-align: center;
  scroll-margin-top: 6rem;
}

.experience-grid-left {
  grid-area: left;
  text-align: center;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.experience-grid-right {
  grid-area: right;
  text-align: center;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.experience-skills-bar {
  display: flex;
  padding-top: 1rem;
  gap: 0.75rem;
}

.dark-mode .experience-filter-toggle div {
  color: #f5f5f5;
}
.experience-filter-toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.experience-filter-toggle div {
  cursor: pointer;
  border: 2px solid black;
  padding: 0.5rem;
}

.experience-filter-toggle div:hover {
  background-color: #3c7a89;
  color: white;
}
